/* Fix STYLING ISSUES!!! */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
}

body {
  background:  rgb(253, 97, 61);
  font-family: 'Roboto', sans-serif;
  color: rgb(0, 0, 0);
}

/* make sure logo aligned correctly */


.nav-bar {
  margin-top: 20px;
  padding-left: 1em;
  padding-right: 1em;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  padding-bottom: 2vh;
  
  .logo {
    width: 120px;
    padding-bottom: 10px;
    }
  
  button {
    font-size: 1.5em;
    font-weight: bold;
    padding: 10px;
    color: rgb(0, 0, 0);
    border: 1px solid black;
    border-radius: 15px;
    background-color: rgb(61, 125, 202);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  button:hover {
    transform: scale(1.01);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    color: #f0f0f1
  }

  .stats{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;

    .timer {
      font-size: 1.5em;
      font-weight: bold;
      padding: 10px 30px;
      font-size: 1.5em;
      font-weight: bold;
      background-color: rgb(61, 125, 202);
      border-radius: 10px 0px 0px 10px;
      border: 1px solid black;
    }
  
    .score {
      font-size: 1.5em;
      font-weight: bold;
      padding: 10px 30px;
      font-size: 1.5em;
      font-weight: bold;
      background-color: rgb(61, 125, 202);
      border: 1px solid black;
    }
  
    .best-score {
      padding: 10px 30px;
      font-size: 1.5em;
      font-weight: bold;
      background-color: rgb(61, 125, 202);
      border-radius: 0px 10px 10px 0px;
      border: 1px solid black;
    }
  }
}

.rules {
  padding-left: 10px;
  margin-top: 5px;
}

hr {
  border: 1px solid black;
  margin-top: 10px;
  margin-bottom: 10px;
}

.start-grid {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;

  .start-button {
    font-size: 2.5em;
    width: 20vh;
    height: 20vh;
    padding: 10px;
    margin: 10%;
    color: rgb(0, 0, 0);
    border: 1px solid black;
    border-radius: 15px;
    background:  rgb(255, 204, 2);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
}

.cardGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr)); /* Adjust column width as needed */
  grid-gap: 15px; /* Adjust as needed for spacing between cards */
  padding: 10px;

  .card {
    background-color: rgb(61, 125, 202);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border: 1px solid black;
    border-radius: 5px;
    background-color: rgb(61, 125, 202);
    transition: all 0.1s ease-in-out;
  }
  
  .card-name {
    padding-top: 10px;
    background-color: rgb(61, 125, 202);
    font-family:Impact, Haettenschweiler, 'Arial Narrow', sans-serif;
  }
  
  .card:hover {
    transform: scale(1.01);
    background-color: rgb(61, 125, 202);
    box-shadow: 0 0 10px rgba(0, 0, 0, 1);
  }
  
  .card img {
    background-color: rgb(61, 125, 202);
    width: 150px;
    height: 150px;
    padding-bottom: 10px;
    border-bottom: 3px solid black;
  }
}
